
import { defineComponent } from "vue";
import AppSidebar from "@/components/AppSidebar.vue";

export default defineComponent({
  components: { AppSidebar },
  data: () => ({
    isLoad: false
  }),
  async mounted() {
    this.isLoad = false;
    try {
      await this.$store.dispatch('getRooms');
      this.isLoad = true;
    } catch (error) {
      console.log(error)
    }
  }
});

